@import url(https://fonts.googleapis.com/css?family=Roboto:400);
@import "libs";
@import "reset";
@import "swiper";
@import "vars";

body {
    font-family: $roboto;
    font-size: 14px;
    background: #181818;
}

header {
    max-width: 960px;
    margin: 0 auto;
}

.logo {
    width: 100px;
    height: 32px;
    padding-top: 5vh;
    fill: #fff;

    &:hover {
        fill: $tomato;
        transition: fill $speed ease-in-out;
    }

    &:hover .logo--v {
        fill: tomato;
        transition: fill $speed ease-in-out;
        animation: move 0.6s ease-in-out;
        @keyframes move {
            0% {
                transform: rotate(0deg);
            }

            99% {
                transform: rotate(350deg);
            }

            100% {
                transform: rotate(-360deg);
            }
        }
    }

    .logo--bieno,
    .logo--v {}
}
/* MAIN MENU */
.menu {
  display: none;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    height: 100vh;
    // height: nth($menu-size,2);
    width: nth($menu-size,1);
    overflow: hidden;
    pointer-events: none;
    user-select: none;

    * {
        backface-visibility: hidden;
    }

    #menuTrigger:checked ~ & {
        pointer-events: auto;

        .link {
            transition-delay: $speed/2;
            @for $i from 0 to 5 {
                &:nth-child(#{$i + 1}) {
                    transform: translate3d(0,0,0);
                }
            }
        }
    }

    .links {
        position: relative;
        z-index: 1;
        list-style: none;
        color: $white;
        margin: $button-size + $padding*3 0 0 0;
        padding: 0;
    }

    .link {
        font-size: 16px;
        letter-spacing: 1px;
        line-height: 1;
        text-align: center;
        margin: 0;
        padding: 0;
        transition: transform $speed ease-in-out;

        &:last-child {
            margin-bottom: 50vh;
        }
        @for $i from 1 through 5 {
            &:nth-child(#{$i}) {
                transform: translate3d(100%*$i,0,0);
            }
        }

        a {
            display: block;
            color: currentColor;
            padding: $padding;
            text-decoration: none;

            &:hover {
                background-color: rgba(white,0.1);
            }
        }
    }

    .menu__trigger {
        position: absolute;
        z-index: 0;
        top: 3vh;
        right: 3vh;
        height: $button-size;
        width: $button-size;
        border-radius: 100%;
        cursor: pointer;
        pointer-events: auto;
        transition: all $speed ease-in-out;
        transition-property: transform, box-shadow;
        transform: translate3d(0,0,0);
        box-shadow: 0 0 0 0 $tomato;
        background-color: rgba(white, 0.2);

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            // height: 100%;
            width: 100%;
            border-radius: 100%;
            overflow: hidden;
            transform: translate3d(0,0,0);
            transition: box-shadow $speed/2 $speed ease-in-out;
            box-shadow: 0 0 0 $button-size/2 $gray inset, 0 0 0 $button-size/2 $tomato inset;
        }

        #menuTrigger:checked ~ & {
            transform: translate3d(0,0,0);
            box-shadow: 0 0 0 nth($menu-size,2) $gray;

            &:before {
                transition-delay: 0s;
                box-shadow: 0 0 0 0 $gray inset, 0 0 0 $button-size/2 $tomato inset;
            }

            .line {
                &:nth-child(1) {
                    transform: translateY(0) translate3d(-50%, -50%, 0);
                }

                &:nth-child(3) {
                    transform: translateY(0) translate3d(-50%, -50%, 0);
                }
            }
        }

        .line {
            position: absolute;
            top: 50%;
            left: 50%;
            background: $white;
            height: 2px;
            width: $button-size/3;
            transform: translate3d(-50%, -50%, 0);
            transition: transform $speed ease-in-out;

            &:nth-child(1) {
                transform: translateY(-5px) translate3d(-50%, -50%, 0);
            }

            &:nth-child(3) {
                transform: translateY(5px) translate3d(-50%, -50%, 0);
            }
        }
    }
}

#menuTrigger {
    position: fixed;
    top: 0;
    right: -20px;
}
/* END OF MAIN MENU */
/*SWIPER*/
.swiper-container {
    width: 100%;
    padding-top: 12vh;
    // margin: 12vh auto 0;
    padding-bottom: 15vh;
    color: #fff;
    display: flex;
    justify-content: center;
}

.swiper-slide {
    position: relative;
    text-align: center;
    font-size: 18px;
    width: 670px;
    height: 447px;

    .title__container {
        position: absolute;
        top: 20%;
        left: -10%;
        height: 4rem;
        overflow: hidden;
    }

    .subtitle,
    .title {
        font-size: 4rem;
        font-weight: 500;
        transform: translate(0, 4rem);
        transition: transform 1.5s cubic-bezier(.23,1,.32,1);
    }
}

.swiper-slide-active {
    position: relative;

    .title {
        transform: translate(0, 0rem);
        transition: transform 2s cubic-bezier(.23,1,.32,1);
    }
}
// Title hover effect

.text-hover {
    position: relative;
    overflow: hidden;
    text-transform: uppercase;
    color: #fff;
}

.text-hover:hover {
    color: $tomato;
}

.text-hover::after {
    content: '';
    position: absolute;
    height: 16px;
    width: 101%;
    top: 50%;
    margin-top: -8px;
    right: 0;
    background: #fff;
    transform: translate3d(-100%,0,0);
    transition: transform 0.4s;
    transition-timing-function: cubic-bezier(0.7,0,0.3,1);
}

.text-hover:hover::after {
    transform: translate3d(100%,0,0);
}

.text-hover::before {
    content: attr(data-letters);
    position: absolute;
    z-index: 2;
    overflow: hidden;
    color: #fff;
    white-space: nowrap;
    width: 0;
    transition: width 0.4s 0.3s;
}

.text-hover:hover::before {
    width: 100%;
}
// End of Title hover effect

.parallax-bg {
    position: absolute;
    width: 200vw;
    height: 100%;

    &:after {
        opacity: 0.5;
        color: rgba(255, 255, 255, 0.04);
        content: 'front-end dev';
        font-size: 160px;
        font-weight: 600;
        text-transform: uppercase;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.swiper-pagination-bullets {
    width: 550px;
    // width: 40vw !important;
    background: url("../img/keyboard_arrow_left.svg") -1.3% 50% no-repeat, url("../img/pagination_dot.png") 0 50% repeat-x, url("../img/keyboard_arrow_right.svg") 101.3% 50% no-repeat,;
}

.swiper-pagination-bullet {
    visibility: hidden;
}

.swiper-pagination-bullet-active {
    visibility: visible;
    position: relative;
    padding-top: 1px;
    line-height: 16px;
    vertical-align: middle;
    color: white;
    font-size: 12px;
}

.swiper-pagination-white .swiper-pagination-bullet-active {
    background: #181818;
}

.pagindex-container {
    display: inline-block;
    width: 58px;
    background: #262626;
}

.footer {
    position: relative;
    max-width: 960px;
    margin: 0 auto;
    z-index: 1;

    &__email,
    &__socials {
        position: absolute;
        top: -15px;
        margin-left: 10px;

        a {
            font-size: 13px;
            color: #fff;
            opacity: 0.2;
            transition: opacity 0.4s cubic-bezier(.23,1,.32,1);

            &:hover {
                opacity: 1;
                cursor: pointer;
            }
        }
    }

    &__socials {
        right: 10px;

        .icon {
            margin-left: 10px;
        }
    }
}
@import "media";
